import { Box, Button, Center, Divider, Grid, GridItem, HStack, Heading, Switch, Text, VStack } from "@chakra-ui/react"

export const Setting = () => {
    return (
        <Box maxW='800px' m='50px auto 100px'>
            <Center m='50px 0'>
                <Heading></Heading>
            </Center>
            <Divider />
            <Grid templateColumns='repeat(4, 1fr)' gap={5} m='50px auto 20px'>
                <GridItem colSpan={1}><Heading size='md'> </Heading></GridItem>
                <GridItem colSpan={2}><Text fontSize='lg'>{` Sujin's Blog`}</Text></GridItem>
                <GridItem colSpan={1}><Button variant='outline' size='sm'></Button></GridItem>
                <GridItem colSpan={4} pb='30px'><Text fontSize='lg'>     .</Text></GridItem>

                <GridItem colSpan={1}><Heading size='md'> </Heading></GridItem>
                <GridItem colSpan={2}><Text fontSize='lg'> </Text></GridItem>
                <GridItem colSpan={1}><Text fontSize='lg'></Text></GridItem>
                <GridItem colSpan={4} pb='30px'><Text fontSize='lg'>    .</Text></GridItem>

                <GridItem colSpan={1}><Heading size='md'></Heading></GridItem>
                <GridItem colSpan={2}><Text fontSize='lg'>shinsj5037@gmail.com</Text></GridItem>
                <GridItem colSpan={1}><Button variant='outline' size='sm'></Button></GridItem>
                <GridItem colSpan={4} pb='30px'><Text fontSize='lg'>      .</Text></GridItem>

                <GridItem colSpan={1}><Heading size='md'>  </Heading></GridItem>
                <GridItem colSpan={3}><HStack><Text fontSize='lg'> </Text><Switch size='md' /></HStack></GridItem>
                <GridItem colSpan={1}></GridItem>
                <GridItem colSpan={3}><HStack><Text fontSize='lg'>  </Text><Switch size='md' /></HStack></GridItem>
                <GridItem colSpan={4} pb='30px'><Text fontSize='lg'>      .</Text></GridItem>

                <GridItem colSpan={1}><Heading size='md'> </Heading></GridItem>
                <GridItem colSpan={3}><Button variant='outline' size='sm'> </Button></GridItem>
                <GridItem colSpan={4} pb='30px'><Text fontSize='lg'>         .</Text></GridItem>
            </Grid>
            <Divider />
        </Box>
    )
}